.home-content {
  margin: 0 8.33%;
}

.home-main {
  padding-top: 30vh;
}

.header-main {
  text-align: center;
}

.header-info {
  max-width: 500px;
  margin: 0 auto;
  font-size: 1.1rem;
  text-align: center;
}

.zipform-container {
  width: 90%;
  max-width: 500px;
  margin: 0 auto;
}

.container-zipform {
  max-width: 280px;
  margin: 0 auto;
}

.container-header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.header-home {
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: calc(2rem - 0.14285714em) 0 1rem;
  padding-left: 5%;
}

.header-results {
  margin: 0 auto;
}

.header-spacer {
  -webkit-flex: 1 1;
          flex: 1 1;
  padding-right: 5%;
}

.plan-viewer {
  margin: 0.75rem 1%;
}

.company-logo {
  height: 2.2rem;
  width: 4.5rem;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: contain;
}

/* turns off top borders for semantics ui */
.ui.table tr td {
  border-top: 0;
}

/* overrides padding from  cell with checkbox */
.ui.table td.checkbox-cell {
  padding-right: 3px;
  padding-left: 0;
}

/* overrides padding from  cell with company */
.ui.table td.company-cell {
  padding-right: 0;
  padding-left: 3px;
}

.accordion-row {
  border-bottom: 1px solid rgba(34, 36, 38, 0.1);
  border-bottom-style: solid;
  border-bottom-color: rgba(34, 36, 38, 0.1);
}

.plan-dropdown:hover {
  background-color: #e2f2fd;
  cursor: pointer;
}

.plan-dropdown {
  background-color: rgb(249, 250, 251);
  margin: 0 auto;
  width: 10em;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-top: 1px solid rgba(34, 36, 38, 0.15);
  border-left: 1px solid rgba(34, 36, 38, 0.15);
  border-right: 1px solid rgba(34, 36, 38, 0.15);
}

.plan-dropup {
  background-color: rgb(249, 250, 251);
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border-bottom: 1px solid rgba(34, 36, 38, 0.15);
  border-left: 1px solid rgba(34, 36, 38, 0.15);
  border-right: 1px solid rgba(34, 36, 38, 0.15);
  margin: 0 auto;
  width: 10em;
  height: 2rem;
}

.plan-dropup:hover {
  background-color: #e2f2fd;
  cursor: pointer;
}

.dropdown-icon {
  margin: 0 auto !important;
  display: block !important;
}

.dropup-icon {
  margin: 0 auto !important;
  display: block !important;
}

.hidden {
  padding: 0 !important;
  height: 0;
  visibility: hidden;
}

.ui.table.details-table {
  border-left-width: 0;
  border-right-width: 0;
  font-size: 0.85em;
}

/* Overriding sortable tag on nested table from semantics ui */
.ui.table.details-table thead th {
  cursor: default;
}

/* Prevents table headers from appearing sortable in nested table */
.ui.table.details-table thead th:hover {
  background: rgb(249, 250, 251);
  color: rgba(0, 0, 0, 0.87);
}

